const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
		igo:0,
		bookmark:0,
		city:[],
        rr:"ав",
		}


	}
	componentWillMount() {
		const {your_city,city,title}=this.props;
		this.setState({your_city:your_city,city:city,title:title})
	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	ret2(item,i){

		return(
			<Cell
				onClick={()=>{

					this.setState({activePanel: "home",

					})



				}}
				data-to="home"
				asideContent={this.state.your_city === item ? <Icon24Done fill="var(--accent)" /> : null}
			>
				{item}
			</Cell>

		);
	}

	render() {

		const {setView}=this.props;

		return (


			<View id="my" activePanel="my">
				<Panel id="my">
					<PanelHeader
						left={<HeaderButton onClick={setView} data-to="glav">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
						Мои мероприятия
					</PanelHeader>


				</Panel>
			</View>

		);
	}
}
